export default {
	data() {
		return {
			image:'',
			title:'',
			content:""
		}
	},
	onLoad(e) {
		console.log(e)
		uni.showLoading({
			title:"加载中...."
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
			//	console.log(res)
				//this.image=res.data.author_avatar;
				this.image=res.data.cover;
				this.title=res.data.title;
				this.content=res.data.content;
				uni.hideLoading()
			},
			fail: () => {
				uni.hideLoading()
			},
			complete: () => {}
		});
	},
	methods: {
		
	}
}
